import React from "react"
import ChartistGraph from "react-chartist"

const stackedbarchart = () => {
  var barChartData = {
    labels: ["John Smith", "Sarah Jane", "Steve Jones", "Maria Revera", "Karl Jacobs", "Trevor Lavour"],
    series: [
      [800000, 1200000, 1400000, 1300000, 1520000, 1400000],
      [200000, 400000, 500000, 300000, 452000, 500000],
      [160000, 290000, 410000, 600000, 588000, 410000],
    ],
  }
  var barChartOptions = {
    stackBars: true,
    colors: ["#34c38f", "#f1b44c", "#E5006B"],
    axisY: {
      labelInterpolationFnc: function (value) {
        return value / 1000 + "k"
      },
    },
  }
  return (
    <React.Fragment>
      <ChartistGraph
        data={barChartData}
        style={{ height: "359px" }}
        options={barChartOptions}
        type={"Bar"}
      />
    </React.Fragment>
  )
}

export default stackedbarchart
