import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { Col, Card, CardBody, Row, Container, Button, Modal, ModalBody, Label } from "reactstrap"
import LoadingSpinnerThreeDots from '../../helpers/commonhelpers/loadingSpinner'
import Breadcrumbs from "../../components/Common/Breadcrumb"
import { getUserList, deactivateUser, deleteUser, getAllMerchantIds, getMerchantList } from 'store/actions'
import BootstrapTable from "react-bootstrap-table-next"
import paginationFactory, {
    PaginationListStandalone,
    PaginationProvider,
} from "react-bootstrap-table2-paginator"
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit"
import UserColumns from './UserColumns'
import { useDispatch } from "react-redux"
import AuthUser from "components/AuthUser/AuthUser"
import { IsEmpty } from "react-lodash"
import { ToastContainer } from 'react-toastify';
import UserCreatePopUp from "./UserCreatePopUp"
import SweetAlert from "react-bootstrap-sweetalert"
import UserUpdatePopUp from "./UserUpdatePopUp"
import { CSVLink } from 'react-csv'


const headersAdmin = [
    { label: 'User Id', key: 'userId' },
    { label: 'Title', key: 'title' },
    { label: 'Merchant Id', key: 'merchantId' },
    { label: 'Username', key: 'userName' },
    { label: 'First Name', key: 'firstName' },
    { label: 'Last Name', key: 'lastName' },
    { label: 'Email', key: 'email' },
    { label: 'Mobile', key: 'mobile' },
    { label: 'Registration Date', key: 'registrationDate' },
    { label: 'Role', key: 'role' },
    { label: 'Department', key: 'department' },
    { label: 'IsActive', key: 'isActive' },
];

const headersSuperAdmin = [
    { label: 'User Id', key: 'userId' },
    { label: 'Title', key: 'title' },
    { label: 'Merchant Id', key: 'merchantId' },
    { label: 'Username', key: 'userName' },
    { label: 'Password', key: 'password' },
    { label: 'First Name', key: 'firstName' },
    { label: 'Last Name', key: 'lastName' },
    { label: 'Email', key: 'email' },
    { label: 'Mobile', key: 'mobile' },
    { label: 'Registration Date', key: 'registrationDate' },
    { label: 'Role', key: 'role' },
    { label: 'Department', key: 'department' },
    { label: 'IsActive', key: 'isActive' },
];
export const index = (props) => {
    const _ = require("lodash");
    const dispatch = useDispatch();
    const [user, setUser] = useState(AuthUser())
    const [{ modal_large, step, userDetails }, setPaymentRequest] = useState({
        modal_large: false,
        step: 0
    })
    const [usersList, setUsersList] = useState([])
    const [loading, setLoading] = useState(props.loading)

    let searchText = "";
    const pageOptions = {
        sizePerPage: 10,
        totalSize: usersList.length, // replace later with size(customerList),
        custom: true,
        onPageChange: (page, sizePerPage) => { handleTableChange() }
    }

    const [merchantIds, setMerchantIds] = useState([])
    const { SearchBar } = Search
    const [confirm_alert, setconfirm_alert] = useState(false)
    const [confirm_alert_activate, setconfirm_alert_activate] = useState(false)
    const [confirm_alert_delete, setconfirm_alert_delete] = useState(false)
    const [success_dlg, setsuccess_dlg] = useState(false)
    const [error_dlg, seterror_dlg] = useState(false)
    const [dynamic_title, setdynamic_title] = useState("")
    const [dynamic_description, setdynamic_description] = useState("")
    const [newUser, setNewUser] = useState("")
    const [updateUser, setUpdateUser] = useState("")
    const [merchantList, setMerchantList] = useState([])
    const [userPassowrd, setUserPassowrd] = useState("")

    let form = "";
    let modalHeader = ""
    //Use Effect for getting customer list with Merchant ID
    useEffect(() => {
        dispatch(getUserList())
        if (user.role === "SuperAdmin") {
            dispatch(getAllMerchantIds())
            dispatch(getMerchantList())
        }
    }, [])

    useEffect(() => {
        if (props.merchantIds !== undefined) {
            setMerchantIds(props.merchantIds)
        }
    }, [props.merchantIds])

    useEffect(() => {
        if (props.merchantList !== undefined) {
            setMerchantList(props.merchantList)
        }
    }, [props.merchantList])

    useEffect(() => { }, [merchantIds])
    useEffect(() => { }, [merchantList])

    useEffect(() => {
        if (newUser !== "") {
            if (newUser === true) {
                setPaymentRequest(formRequest => ({
                    ...formRequest,
                    modal_large: false
                }))
                setsuccess_dlg(true)
                setdynamic_title("Success")
                setdynamic_description("User has been created.")
                dispatch(getUserList())
                setNewUser("")
            }
            else {
                setPaymentRequest(formRequest => ({
                    ...formRequest,
                    modal_large: false
                }))
                seterror_dlg(true)
                setdynamic_title("Fail")
                setdynamic_description("User is not created.")
                setNewUser("")
            }
        }
    }, [newUser, props.loading])

    useEffect(() => {
        if (props.userCreated !== undefined) {
            if (props.userCreated === true) {
                setNewUser(true)
            }
            else {
                setNewUser(false)
            }
        }
    }, [props.userCreated])

    //Use Effect for setting customerList with props
    useEffect(() => {
        setLoading(props.loading)
        if (Array.isArray(props.userList)) {
            if (user.role !== "SuperAdmin") {
                setUsersList(props.userList.filter(
                    users =>
                        users.firstName !== "SELLCO USER" &&
                        users.merchantId === user.merchantId

                ))
            }
            else {
                setUsersList(props.userList)
            }
        }
    }, [props.userList, props.loading])

    useEffect(() => {
        setLoading(props.loading)
    }, [usersList])

    useEffect(() => {
    }, [userDetails])

    useEffect(() => {
        if (props.userUpdated !== undefined) {
            if (props.userUpdated === true) {
                setsuccess_dlg(true)
                setdynamic_title("Success")
                setdynamic_description("User has been updated.")
                tog_large()
                dispatch(getUserList())
            }
        }
    }, [props.userUpdated])


    useEffect(() => {
        if (step === 1) {
            tog_large()
        }
    }, [step])

    useEffect(() => {
        if (modal_large === false) {
            setPaymentRequest(formRequest => ({
                ...formRequest,
                step: 0
            }))
        }
    }, [modal_large])

    useEffect(() => {
        if (props.userDeactivate !== undefined) {
            if (props.userDeactivate === true) {
                setsuccess_dlg(true)
                setdynamic_title("Success")
                dispatch(getUserList())
            }
            if (props.userDeactivate === false) {
                seterror_dlg(true)
                setdynamic_title("Fail")
            }
        }
    }, [props.userDeactivate])

    useEffect(() => {
        if (props.userDelete !== undefined) {
            if (props.userDelete === true) {
                setsuccess_dlg(true)
                setdynamic_title("Success")
                setdynamic_description("User has been deleted.")
                dispatch(getUserList())
            }
            if (props.userDelete === false) {
                seterror_dlg(true)
                setdynamic_title("Fail")
                setdynamic_description("User has not been deleted.")
            }
        }
    }, [props.userDelete])


    useEffect(() => {
        if (updateUser !== "") {
            if (updateUser === true) {
                setPaymentRequest(formRequest => ({
                    ...formRequest,
                    modal_large: false
                }))
                setsuccess_dlg(true)
                setdynamic_title("Success")
                setdynamic_description("User has been updated.")
                dispatch(getUserList())
                setUpdateUser("")
            }

            if (updateUser === false) {
                setPaymentRequest(formRequest => ({
                    ...formRequest,
                    modal_large: false
                }))
                seterror_dlg(true)
                setdynamic_title("Fail")
                setdynamic_description("User is not updated.")
                setUpdateUser("")
            }
        }
    }, [updateUser, props.loading])

    useEffect(() => {
        if (props.userUpdate !== undefined) {

            if (props.userUpdate === true) {
                setUpdateUser(true)
            }
            else {
                setUpdateUser(false)
            }
        }
    }, [props.userUpdate])

    useEffect(() => {
    }, [userPassowrd])

    const handleTableChange = (searchText) => {
        if (searchText !== "" && searchText !== undefined) {
            setUsersList(
                props.userList.filter(customer =>
                    Object.keys(customer).some(key =>
                        customer[key].toLowerCase().includes(searchText.toLowerCase())
                    )
                )
            )
        }
    }


    //Function for opening modal
    function tog_large() {
        setPaymentRequest(formRequest => ({
            ...formRequest,
            modal_large: !modal_large
        }))
        removeBodyCss()
    }

    function removeBodyCss() {
        document.body.classList.add("no_padding")
    }

    const rowEvents = {
        onClick: (e, row, rowIndex) => {
            setPaymentRequest(formRequest => ({
                ...formRequest,
                userDetails: row
            }))
        }
    };

    const handleActionTableUser = (value) => {
        if (value === "update") {
            setPaymentRequest(formRequest => ({
                ...formRequest,
                step: 1
            }))
        }
        if (value === "deactivate") {
            setconfirm_alert(true)
        }
        if (value === "activate") {
            setconfirm_alert_activate(true)
        }
        if (value === "delete") {
            setconfirm_alert_delete(true)
        }

    }

    const handleDeactivateUser = () => {
        setdynamic_description("Deactivated")
        setconfirm_alert(false)
        if (userDetails !== undefined) {
            dispatch(deactivateUser(userDetails.userId))
        }
    }
    const handleActivateUser = () => {
        setdynamic_description("Activated")
        setconfirm_alert_activate(false)
        if (userDetails !== undefined) {
            dispatch(deactivateUser(userDetails.userId))
        }
    }

    const handleDeleteUser = () => {
        
        setconfirm_alert_delete(false)
        let userDeleteConfirm ={
            Password:userPassowrd,
            Email:userDetails._id
        }
        if (userDeleteConfirm.Passowrd !== "" && userDeleteConfirm.Email !== undefined ) {
            dispatch(deleteUser(userDeleteConfirm))
            setUserPassowrd("")
        }
    }

    switch (step) {
        case 0:
            form = <UserCreatePopUp
                merchantIds={merchantIds} />
            modalHeader = <Row className="ml-1 my-3">
                <Label>ADD NEW USER</Label>
            </Row>
            break;
        case 1:
            form = <UserUpdatePopUp
                userDetails={userDetails}
                merchantIds={merchantIds}
                onChange={value => setPaymentRequest(value)} />
            modalHeader = <Row className="ml-1 my-3">
                <Label>UPDATE USER</Label>
            </Row>
            break;
        default:
            break;
    }

    return (


        <React.Fragment>
            <div className="page-content">
                <Container fluid>

                    <Breadcrumbs title="MANAGE " breadcrumbItem=" USERS LISTS" />


                    <Row>
                        <Col lg="12">
                            {confirm_alert ?
                                <SweetAlert
                                    title="Deactivate User"
                                    warning
                                    showCancel
                                    confirmButtonText="Confirm"
                                    confirmBtnBsStyle="success"
                                    cancelBtnBsStyle="danger"
                                    onConfirm={() => {

                                        handleDeactivateUser();

                                    }}
                                    onCancel={() => setconfirm_alert(false)}
                                >
                                    Deactivate the selected user.
                                </SweetAlert>
                                :
                                null}

                            {confirm_alert_activate ?
                                <SweetAlert
                                    title="Activate User"
                                    warning
                                    showCancel
                                    confirmButtonText="Confirm"
                                    confirmBtnBsStyle="success"
                                    cancelBtnBsStyle="danger"
                                    onConfirm={() => {

                                        handleActivateUser();
                                    }}
                                    onCancel={() => setconfirm_alert_activate(false)}
                                >
                                    Activate the selected user.
                                </SweetAlert>
                                : null}

                            {confirm_alert_delete ?
                                <SweetAlert
                                    title="Delete User"
                                    warning
                                    showCancel
                                    confirmButtonText="Confirm"
                                    confirmBtnBsStyle="success"
                                    cancelBtnBsStyle="danger"
                                    onConfirm={() => {

                                        handleDeleteUser();
                                    }}
                                    onCancel={() => setconfirm_alert_delete(false)}
                                >
                                    Please enter the password to delete the selected user.
                                    <input
                                        type="password"
                                        className="form-control"
                                        value={userPassowrd}
                                        onChange={e => {
                                            setUserPassowrd(e.target.value)
                                        }}
                                    />
                                </SweetAlert>
                                : null}

                            {success_dlg ? (
                                <SweetAlert
                                    timeout={1000}
                                    success
                                    showConfirm={false}
                                    title={dynamic_title}
                                    onConfirm={() => {
                                        setsuccess_dlg(false)
                                    }}
                                >
                                    {dynamic_description}
                                </SweetAlert>
                            ) : null}

                            {error_dlg ? (
                                <SweetAlert
                                    timeout={1000}
                                    error
                                    showConfirm={false}
                                    title={dynamic_title}
                                    onConfirm={() => {
                                        seterror_dlg(false)
                                    }}
                                >
                                    {dynamic_description}
                                </SweetAlert>
                            ) : null}

                            <Modal size="lg"
                                isOpen={modal_large}
                                toggle={() => {
                                    tog_large()
                                }}>
                                <div className="modal-header">
                                    {modalHeader}
                                    <h5
                                        className="modal-title mt-0"
                                        id="myLargeModalLabel"
                                    >

                                    </h5>
                                    <button
                                        onClick={() => {
                                            setPaymentRequest(formRequest => ({
                                                ...formRequest,
                                                modal_large: false
                                            }))
                                        }}
                                        type="button"
                                        className="close"
                                        data-dismiss="modal"
                                        aria-label="Close"
                                    >
                                        <span caria-hidden="true" className="color-danger my-auto"> x</span>
                                    </button>
                                </div>
                                <ModalBody>
                                    {loading ?
                                        <Row className="spinner-custom mx-auto my-auto pb-3">
                                            <LoadingSpinnerThreeDots />
                                        </Row>
                                        : form}
                                </ModalBody>

                            </Modal>
                            <Card>
                                <CardBody>
                                    {loading ?
                                        <Row className="spinner-custom mx-auto my-auto pb-3">
                                            <LoadingSpinnerThreeDots />
                                        </Row>
                                        : <IsEmpty
                                            value={usersList}
                                            yes={() => <>No List</>}
                                            no={() => (
                                                <PaginationProvider
                                                    pagination={paginationFactory(pageOptions)}
                                                >
                                                    {({ paginationProps, paginationTableProps }) => (
                                                        <ToolkitProvider
                                                            keyField="_id"
                                                            data={usersList || []}
                                                            columns={UserColumns(handleActionTableUser, merchantList, user)}
                                                            bootstrap4
                                                            search
                                                        >
                                                            {toolkitProps => (
                                                                <React.Fragment>
                                                                    <Row className="mb-2  mx-2">
                                                                        <Col sm="4">
                                                                            <div className="search-box mr-2 mb-2 d-inline-block">
                                                                                <div className="position-relative">
                                                                                    <SearchBar {...toolkitProps.searchProps} className="width-search" placeholder="Search User/Email/Mobile" />
                                                                                    <i className="bx bx-search-alt search-icon" />
                                                                                </div>
                                                                            </div>
                                                                        </Col>
                                                                        <Col sm="8">
                                                                            <div className="text-sm-right">

                                                                                {user.role === "Admin" ?
                                                                                    <CSVLink
                                                                                        color="warning"
                                                                                        filename={"UserList.csv"}
                                                                                        className="btn btn-warning btn-rounded waves-effect waves-light  mb-2 mr-2"
                                                                                        data={usersList}
                                                                                        headers={headersAdmin}
                                                                                        key="_id"
                                                                                        target='_blank'
                                                                                    >
                                                                                        <i className="bx bx-download label-icon mr-1" />
                                                                                        Download Users
                                                                                    </CSVLink> :
                                                                                    <CSVLink
                                                                                        color="warning"
                                                                                        filename={"UserList.csv"}
                                                                                        className="btn btn-warning btn-rounded waves-effect waves-light  mb-2 mr-2"
                                                                                        data={usersList}
                                                                                        headers={headersSuperAdmin}
                                                                                        key="_id"
                                                                                        target='_blank'
                                                                                        separator={";"}>
                                                                                        <i className="bx bx-download label-icon mr-1" />
                                                                                        Download Users
                                                                                    </CSVLink>}
                                                                                <Button
                                                                                    type="button"
                                                                                    color="success"
                                                                                    className="btn-rounded waves-effect waves-light mb-2 mr-2"
                                                                                    onClick={() => {
                                                                                        tog_large()
                                                                                    }}
                                                                                >
                                                                                    <i className="mdi mdi-account-multiple-plus mr-1" />
                                                                                    New User
                                                                                </Button>
                                                                            </div>
                                                                        </Col>
                                                                    </Row>
                                                                    <Row>
                                                                        <Col xl="12">
                                                                            <div className="table-responsive">
                                                                                <BootstrapTable

                                                                                    responsive
                                                                                    bordered={false}
                                                                                    hover={true}
                                                                                    striped={false}
                                                                                    classes={
                                                                                        "table table-centered pointer table-nowrap table-hover mr-5 mb-5"
                                                                                    }
                                                                                    rowEvents={rowEvents}
                                                                                    keyField="_id"
                                                                                    headerWrapperClasses={"thead-light"}
                                                                                    {...toolkitProps.baseProps}
                                                                                    onTableChange={handleTableChange(searchText)}
                                                                                    {...paginationTableProps}
                                                                                />
                                                                            </div>
                                                                        </Col>
                                                                    </Row>
                                                                    <Row className="align-items-md-center mt-30">
                                                                        <Col className="pagination pagination-rounded justify-content-end mb-2 inner-custom-pagination">
                                                                            <PaginationListStandalone
                                                                                {...paginationProps}
                                                                            />
                                                                        </Col>
                                                                    </Row>
                                                                </React.Fragment>
                                                            )}
                                                        </ToolkitProvider>
                                                    )}
                                                </PaginationProvider>
                                            )} />}
                                    <ToastContainer
                                        position="top-right"
                                        autoClose={3000}
                                        hideProgressBar={false}
                                        newestOnTop={true}
                                        closeOnClick
                                        rtl={false}
                                        pauseOnFocusLoss
                                        draggable
                                        pauseOnHover />
                                </CardBody>
                            </Card>
                        </Col>

                    </Row>
                </Container>
            </div>
        </React.Fragment>
    )
}

const mapStateToProps = state => {
    return {
        loading: state.Users.loading,
        userList: state.Users.usersList,
        userCreated: state.Users.userCreated,
        userDeactivate: state.Users.userDeactivate,
        userDelete: state.Users.userDelete,
        userUpdate: state.Users.update,
        merchantIds: state.Merchants.merchantIds,
        merchantList: state.Merchants.merchantList,
    }
}


export default connect(mapStateToProps)(index)

