import React, { useState, useEffect } from 'react'
import {
    Col,
    Row,
    Button,
    Spinner,
    InputGroup,
    InputGroupAddon,
    NavLink,
    Nav,
    NavItem,
    Label,
    TabContent,
    TabPane,
} from "reactstrap"
import { connect } from "react-redux"
import CustomerColumns from "../Customer/CustomerColumns"
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit"
import { useDispatch } from "react-redux"
import { IsEmpty } from "react-lodash"
import { toast } from 'react-toastify';
import toastr from "toastr"
import "toastr/build/toastr.min.css"
import { updateCustomer, getCustomersByMerchantID, getCustomerList } from "../../store/actions.js"
import BootstrapTable from "react-bootstrap-table-next"
import { useForm } from "react-hook-form";
import AuthUser from "components/AuthUser/AuthUser"
import LoadingSpinnerThreeDots from "helpers/commonhelpers/loadingSpinner";
import paginationFactory, {
    PaginationListStandalone,
    PaginationProvider,
} from "react-bootstrap-table2-paginator"
import 'react-bootstrap-table2-paginator/dist/react-bootstrap-table2-paginator.min.css';
import classnames from "classnames"
import WhatsAppLogo from "../../assets/images/whatsapp.png"

function ExpressPayRequest(props) {
    //Constants----------------------------------------------------------------------------------------------------------------------
    const dispatch = useDispatch();
    const [{ id, Amount, Currency, Name, Email, Mobile,
        MobilePlus, Type, Language, Remarks,
        RegistrationDate, IsActive, MerchantId, PaymentType, activeTabTracker }, setPaymentRequest] = useState({
            Amount: parseFloat(1).toFixed(2),
            Currency: "QAR",
            Remarks: "",
            MobilePlus: "+974",
            Name: "",
            Email: "",
            Mobile: "",
            SearchCustomer: "",
            PaymentType: "Debit, Credit and OMM",
            Language: "EN",
            activeTabTracker: ""
        })
    const { register, handleSubmit, errors } = useForm();
    const [customerList, setCustomerList] = useState({})
    const [user, setUser] = useState(AuthUser())
    const [activeTab, setactiveTab] = useState("1")
    const [loading, setLoading] = useState(props.loading)
    const [merchantCardTypes, setMerchantCardTypes] = useState([])

    let searchText = "";
    const pageOptions = {
        sizePerPage: 3,
        totalSize: customerList.length, // replace later with size(customerList),
        custom: true,
        onPageChange: (page, sizePerPage) => { handleTableChange() }
    }
    const { SearchBar } = Search
    //Use Effects----------------------------------------------------------------------------------------------------
    //Use Effect for getting customer list with Merchant ID
    useEffect(() => {
        UserRole()
    }, [])


    useEffect(() => {
        //console.log(merchant)
        if (merchantCardTypes.length === 0 && props.merchant !== undefined && Array.isArray(props.merchant.paymentGatewayEnrollments)) {
            props.merchant.paymentGatewayEnrollments.map((pgEnrollments, index) => {
                //console.log(pgEnrollments.commissionSettings)
                if (pgEnrollments.isActive === true) {
                    pgEnrollments.commissionSettings.map((cards, i) => {
                        //console.log(cards.paymentMethod)
                        setMerchantCardTypes(merchantCards => [
                            ...merchantCards,
                            cards.paymentMethod
                        ])
                    })
                }
            })
        }
    }, [props.merchant])

    useEffect(() => {
    }, [merchantCardTypes])

    //Use Effect for setting customerList with props
    useEffect(() => {
        setLoading(props.loading)
        if (Array.isArray(props.customersList)) {
            setCustomerList(props.customersList.filter(cus => cus.isActive === true))
        }
    }, [props.customersList, props.loading])

    useEffect(() => { }, [customerList])
    useEffect(() => {
        if (Type === "Recurring") {
            const data = {
                _id: id,
                Name,
                Email,
                Mobile: MobilePlus + "-" + Mobile.toString(),
                RegistrationDate,
                Type,
                MerchantId,
                IsActive
            }
            dispatch(updateCustomer(data))
        }
    }, [Type])

    useEffect(() => { }, [customerList])
    //Functions--------------------------------------------------------------------------------------------------------
    //Function for setting data state changes for this component and props
    const storeDataOnForm = (event) => {
        const { name, value } = event.target;
        setPaymentRequest(formRequest => (
            { ...formRequest, [name]: value }
        ));
        props.onChange(formRequest => (
            { ...formRequest, [name]: value }
        ));
    };
    const UserRole = () => {
        if (user.role !== "SuperAdmin") {
            const data = {
                type: "all",
                merchantId: user.merchantId
            }
            setLoading(props.loading)
            dispatch(getCustomersByMerchantID(data))
        }
        if (user.role === "SuperAdmin") {
            dispatch(getCustomerList())
        }
    }
    function toggle(tab) {
        if (activeTab !== tab) {
            setactiveTab(tab)
            props.onChange(formRequest => (
                {
                    ...formRequest,
                    activeTabTracker: tab
                }
            ));
        }
    }

    //Function for form completion to overview step
    const handleNextStep = (value) => {
        if (Amount !== 0 && Amount !== "" && Amount !== "0" && Amount !== " " && Amount >= 1) {
            if (Name !== "" && Email !== "" && Mobile !== "") {
                props.onChange(formRequest => (
                    {
                        ...formRequest,
                        step: 1
                    }
                ));
                props.onChange(formRequest => ({
                    ...formRequest,
                    emailLoading: value
                }))
            }
            else {
                showToastError("No customer selected.")
            }
        }
        else {
            showToastError("Please enter a valid amount.")
        }
    }


    //Function for click customer in row event
    const rowEvents = {
        onClick: (e, row, rowIndex) => {
            showToastSuccess("Customer " + row.name + " has been selected.")
            if (row.mobile != undefined) {
                let MobileSplit = row.mobile.split("-")
                setPaymentRequest(formRequest => ({
                    ...formRequest,
                    id: row._id,
                    Name: row.name,
                    Email: row.email,
                    Mobile: MobileSplit[1],
                    MobilePlus: MobileSplit[0],
                    RegistrationDate: row.registrationDate,
                    IsActive: row.isActive,
                    MerchantId: row.merchantID,
                    Type: "Recurring",
                }))
                props.onChange(formRequest => ({
                    ...formRequest,
                    Name: row.name,
                    Email: row.email,
                    Mobile: MobileSplit[1],
                    MobilePlus: MobileSplit[0],
                    Type: "Recurring",
                }))
            }
        }
    };

    const handleTableChange = (searchText) => {
        if (searchText !== "" && searchText !== undefined) {
            setCustomerList(
                props.customersList.filter(customer =>
                    Object.keys(customer).some(key =>
                        customer[key].toLowerCase().includes(searchText.toLowerCase())
                    )
                )
            )
        }
    }

    //For form validation, this is needed
    const onSubmit = (data) => {
    }
     //Function for email sending
     const handleEmailSending = (value) => {
        if (Amount !== 0 && Amount !== "" && Amount !== "0" && Amount !== " " && Amount >= 1) {
            if (Name !== "" && Email !== "" && Mobile !== "") {
                props.onChange(formRequest => (
                    {
                        ...formRequest,
                        step: 1
                    }
                ));
               
            }
            else {
                showToastError("No customer selected.")
            }
        }
        else {
            showToastError("Please enter a valid amount.")
        }
       

    }



    const showToastSuccess = (message) => {


        toastr.options = {
            positionClass: "toast-top-right",
            timeOut: 10000,
            extendedTimeOut: 1000,
            closeButton: true,
            debug: false,
            progressBar: true,
            preventDuplicates: true,
            newestOnTop: true,
            showEasing: "swing",
            hideEasing: "linear",
            showMethod: "fadeIn",
            hideMethod: "fadeOut",
            showDuration: 300,
            hideDuration: 300
        }

        toastr.success(message)
    }

    const showToastError = (message) => {


        toastr.options = {
            positionClass: "toast-top-right",
            timeOut: 10000,
            extendedTimeOut: 1000,
            closeButton: true,
            debug: false,
            progressBar: true,
            preventDuplicates: true,
            newestOnTop: true,
            showEasing: "swing",
            hideEasing: "linear",
            showMethod: "fadeIn",
            hideMethod: "fadeOut",
            showDuration: 300,
            hideDuration: 300
        }

        toastr.error(message)
    }

    return (
        <Row className="form-group mx-1" onSubmit={handleSubmit(onSubmit)}>
            <Col lg="12 " className="">


                <InputGroup className="mb-3">
                    <InputGroupAddon addonType="prepend">
                        <Label className="input-group-text input-text-size">
                            AMOUNT
                        </Label>
                    </InputGroupAddon>
                    <select className="form-control  custom-select input-text-size"
                        id="Currency"
                        name="Currency"
                        style={{ maxWidth: "90px" }}
                        value={Currency}
                        ref={register({ required: true })}
                        onChange={(event) => storeDataOnForm(event, props.setFormRequest)}>
                        <option>QAR</option>
                        <option>USD</option>
                    </select>
                    <input
                        id="Amount"
                        name="Amount"
                        placeholder="0.01"
                        step="0.01"
                        min={1}
                        value={Amount}
                        type="number"
                        className="form-control  input-text-size"
                        ref={register({ required: true, min: 1 })}
                        onChange={(event) => storeDataOnForm(event, props.setFormRequest)} />
                </InputGroup>
                {errors.Amount && errors.Amount.type === "required" && <p className="color-danger">This is required</p>}
                {errors.Amount && errors.Amount.type === "min" && <p className="color-danger">Amount is 0, please enter valid</p>}

                <Row>
                    <Col lg="6">
                        <InputGroup className="mb-3">
                            <InputGroupAddon addonType="prepend">
                                <Label className="input-group-text input-text-size">
                                    CARD TYPE
                                </Label>
                            </InputGroupAddon>

                            {merchantCardTypes.length === 3 ?
                                <select className="form-control form-control-lg input-text-size"
                                    id="PaymentType"
                                    name="PaymentType"
                                    value={PaymentType}
                                    ref={register({ required: true })}
                                    onChange={(event) => storeDataOnForm(event, props.setFormRequest)}>

                                    <option value="Debit, Credit and OMM" >Debit, Credit and OMM</option>
                                    <option value="Debit and Credit">Debit and Credit</option>
                                    <option value="OMM">OMM</option>
                                    <option value="Credit">Credit</option>
                                    <option value="Debit">Debit</option>
                                </select>
                                : null}

                            {merchantCardTypes.length === 2 ?
                                <select className="form-control form-control-lg input-text-size"
                                    id="PaymentType"
                                    name="PaymentType"
                                    value={PaymentType}
                                    ref={register({ required: true })}
                                    onChange={(event) => storeDataOnForm(event, props.setFormRequest)}>

                                    <option value="Debit and Credit">Debit and Credit</option>
                                    <option value="Debit">Debit</option>
                                    <option value="Credit">Credit</option>
                                </select>
                                : null}
                        </InputGroup>
                    </Col>
                    <Col lg="6">
                        <InputGroup className="">
                            <InputGroupAddon addonType="prepend">
                                <Label className="input-group-text input-text-size">
                                    LANGUAGE
                                </Label>
                            </InputGroupAddon>
                            <select className="form-control form-control-lg input-text-size"
                                id="Language"
                                name="Language"
                                value={Language}
                                ref={register({ required: true })}
                                onChange={(event) => storeDataOnForm(event, props.setFormRequest)}>
                                <option>EN</option>
                                <option>AR</option>
                            </select>
                        </InputGroup>

                    </Col>
                </Row>



                <Label className=" input-text-size">
                    REMARKS
                </Label>
                <textarea
                    id="Remarks"
                    name="Remarks"
                    value={Remarks}
                    className="form-control form-control-lg input-text-size"
                    rows="1"
                    style={{ resize: "none" }}
                    ref={register({ required: false })}
                    onChange={(event) => storeDataOnForm(event, props.setFormRequest)}
                />


                <Nav pills className="navtab-bg nav-justified mt-3">
                    <NavItem>
                        <NavLink
                            style={{ cursor: "pointer" }}
                            className={classnames({
                                active: activeTab === "1",
                            })}
                            onClick={() => {
                                toggle("1")
                            }}
                        >
                            {/* <i className=" fas fa-search tab-icon color-primary" /> */}
                            Add customer details
                        </NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink
                            style={{ cursor: "pointer" }}
                            className={classnames({
                                active: activeTab === "2",
                            })}
                            onClick={() => {
                                toggle("2")
                            }}
                        >
                            {activeTab === "1" ?
                                <i className=" fas fa-search tab-icon color-primary" /> :
                                <i className=" fas fa-search tab-icon color-secondary" />}
                        </NavLink>
                    </NavItem>

                </Nav>
                <TabContent activeTab={activeTab}>
                    <TabPane tabId="1" className="p-3">

                        <Row>
                            <Col>
                                <InputGroup className="mb-3">
                                    <InputGroupAddon addonType="prepend">
                                        <Label className="input-group-text input-text-size">
                                            NAME
                                        </Label>
                                    </InputGroupAddon>
                                    <input
                                        id="Name"
                                        name="Name"
                                        value={Name}
                                        type="text"
                                        className="form-control form-control-lg input-text-size"
                                        ref={register({ required: false })}
                                        onChange={(event) => storeDataOnForm(event, props.setFormRequest)}
                                    />
                                </InputGroup>
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                <InputGroup className="mb-3">
                                    <InputGroupAddon addonType="prepend">
                                        <Label className="input-group-text input-text-size">
                                            EMAIL
                                        </Label>
                                    </InputGroupAddon>
                                    <input
                                        id="Email"
                                        name="Email"
                                        value={Email}
                                        type="email"
                                        ref={register({ required: false })}
                                        className="form-control  form-control-lg input-text-size"
                                        onChange={(event) => storeDataOnForm(event, props.setFormRequest)}
                                    />
                                </InputGroup>
                            </Col>

                        </Row>
                        <Row>
                            <Col >
                                <InputGroup className="mb-3">
                                    <InputGroupAddon addonType="prepend">
                                        <Label className="input-group-text input-text-size">
                                            MOBILE
                                        </Label>
                                    </InputGroupAddon>
                                    <select className="form-control  custom-select input-text-size"
                                        id="MobilePlus"
                                        name="MobilePlus"
                                        value={MobilePlus}
                                        ref={register({ required: true })}
                                        onChange={(event) => storeDataOnForm(event, props.setFormRequest)}>
                                        <option value="+974" defaultValue>+974</option>
                                    </select>
                                </InputGroup>
                            </Col>
                            <Col >
                                <InputGroup className="mb-3">
                                    <input
                                        id="Mobile"
                                        name="Mobile"
                                        value={Mobile}
                                        type="number"
                                        ref={register({ required: false })}
                                        className="form-control  form-control-lg input-text-size"
                                        onChange={(event) => storeDataOnForm(event, props.setFormRequest)}
                                    />
                                </InputGroup>
                            </Col>
                        </Row>
                    </TabPane>
                    <TabPane tabId="2" className="p-3">
                        {loading ?
                            <Row className="spinner-custom mx-auto my-auto pb-3">
                                <LoadingSpinnerThreeDots />
                            </Row>
                            :
                            <Row>
                                <Col>
                                    <IsEmpty
                                        value={customerList}
                                        yes={() => <></>}
                                        no={() => (
                                            <PaginationProvider
                                                pagination={paginationFactory(pageOptions)}
                                            >
                                                {({ paginationProps, paginationTableProps }) => (
                                                    <ToolkitProvider
                                                        keyField="_id"
                                                        data={customerList || ["No customers"]}
                                                        columns={CustomerColumns()}
                                                        bootstrap4
                                                        search
                                                    >
                                                        {toolkitProps => (
                                                            <React.Fragment>
                                                                <Row className="mb-2  mx-2">
                                                                    <Col className="item-center">
                                                                        <div className="search-box mr-2 mb-2 d-inline-block">
                                                                            <div className="position-relative input-text-size">
                                                                                <SearchBar {...toolkitProps.searchProps} className="width-search" placeholder="Search Customer/Email/Mobile" />
                                                                                <i className="bx bx-search-alt search-icon" />
                                                                            </div>
                                                                        </div>
                                                                    </Col>
                                                                </Row>
                                                                <Row>
                                                                    <Col xl="12">
                                                                        <div className="table-responsive">
                                                                            <BootstrapTable
                                                                                responsive
                                                                                bordered={false}
                                                                                hover={true}
                                                                                striped={false}
                                                                                classes={
                                                                                    "table table-centered pointer table-nowrap table-hover"
                                                                                }
                                                                                rowEvents={rowEvents}
                                                                                keyField="_id"
                                                                                headerWrapperClasses={"thead-light"}
                                                                                {...toolkitProps.baseProps}
                                                                                onTableChange={handleTableChange(searchText)}
                                                                                {...paginationTableProps}
                                                                            />
                                                                        </div>
                                                                    </Col>
                                                                </Row>
                                                                <Row className="align-items-md-center mt-30">
                                                                    <Col className="pagination pagination-rounded justify-content-end mb-2 inner-custom-pagination">
                                                                        <PaginationListStandalone
                                                                            {...paginationProps}
                                                                        />
                                                                    </Col>
                                                                </Row>
                                                            </React.Fragment>
                                                        )}
                                                    </ToolkitProvider>
                                                )}
                                            </PaginationProvider>
                                        )} />
                                </Col>
                            </Row>
                        }

                        <Row>
                            <Col>
                                <InputGroup className="mb-3">
                                    <InputGroupAddon addonType="prepend">
                                        <Label className="input-group-text input-text-size">
                                            NAME
                                        </Label>
                                    </InputGroupAddon>
                                    <input
                                        id="Name"
                                        name="Name"
                                        value={Name}
                                        type="text"
                                        className="form-control bg-color-disable form-control-lg input-text-size"
                                        readOnly
                                        ref={register({ required: false })}
                                        onChange={(event) => storeDataOnForm(event, props.setFormRequest)}
                                    />
                                </InputGroup>
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                <InputGroup className="mb-3">
                                    <InputGroupAddon addonType="prepend">
                                        <Label className="input-group-text input-text-size">
                                            EMAIL
                                        </Label>
                                    </InputGroupAddon>
                                    <input
                                        id="Email"
                                        name="Email"
                                        value={Email}
                                        type="email"
                                        readOnly
                                        ref={register({ required: false })}
                                        className="form-control bg-color-disable form-control-lg input-text-size"
                                        onChange={(event) => storeDataOnForm(event, props.setFormRequest)}
                                    />
                                </InputGroup>
                            </Col>

                        </Row>
                        <Row>
                            <Col >
                                <InputGroup className="mb-3">
                                    <InputGroupAddon addonType="prepend">
                                        <Label className="input-group-text input-text-size">
                                            MOBILE
                                        </Label>
                                    </InputGroupAddon>
                                    <input
                                        id="MobilePlus"
                                        name="MobilePlus"
                                        value={MobilePlus}
                                        readOnly
                                        type="text"
                                        ref={register({ required: false })}
                                        className="form-control bg-color-disable form-control-lg input-text-size"
                                        onChange={(event) => storeDataOnForm(event, props.setFormRequest)}
                                    />
                                </InputGroup>
                            </Col>
                            <Col >
                                <InputGroup className="mb-3">
                                    <input
                                        id="Mobile"
                                        name="Mobile"
                                        value={Mobile}
                                        readOnly
                                        type="number"
                                        ref={register({ required: false })}
                                        className="form-control bg-color-disable form-control-lg input-text-size"
                                        onChange={(event) => storeDataOnForm(event, props.setFormRequest)}
                                    />
                                </InputGroup>
                            </Col>
                        </Row>
                    </TabPane>
                </TabContent>


                {/* <Row>
                    <Col className="mx-auto item-center">
                        <Button
                            color="primary"
                            className=" waves-effect waves-light btn-lg"
                            onClick={handleNextStep}
                        >
                            Create Payment Request
                        </Button>
                    </Col>
                </Row> */}
                <Row>
                    <Col className="item-center">
                    <div style={{ margin: "5px" }}>
                        <Button
                            data-tip="<div><small>Send invoice to customer Whatsapp.</small></div> "
                            data-html={true}
                            type="submit"
                            className="color-whatsapp  border-none"
                            onClick={() => handleNextStep("whatsapp")}
                        >
                            <img src={WhatsAppLogo} className="whatsapplogo" />
                            WhatsApp
                        </Button>
                        </div>
                        <div style={{ margin: "5px" }}>
                            <Button
                                data-tip="<div><small>Sends out request</small> </div> <div><small>to your email without</small></div>  <div><small> notifying the customer.</small></div> "
                                data-html={true}
                                type="submit"
                                color="primary"
                                onClick={() => handleNextStep("user")}
                            >
                                SEND TO MY EMAIL
                            </Button>
                        </div>
                        <div style={{ margin: "5px" }}>
                            <Button
                                data-tip="<div><small>Sends out request</small> </div> <div><small>to customer including you</small></div>  <div><small> and the registered</small></div>  <div><small> notification emails.</small></div> "
                                data-html={true}
                                type="submit"
                                color="success"
                                onClick={() => handleNextStep("all")}
                            >
                                SEND TO CUSTOMER
                            </Button>
                        </div>
                        
                    </Col>
                </Row>
            </Col>
        </Row>
    )
}
const mapStateToProps = state => {
    return {
        customer: state.Customer.customer,
        loading: state.Customer.loading,
        customersList: state.Customer.customerList
    }
}

export default connect(mapStateToProps)(ExpressPayRequest)
