import React from "react"
import dateFormat from '../../helpers/commonhelpers/dateFormat'

const UserLogsColumns = (merchantList, userList) => [
    {
        dataField: "refId",
        text: "Reference Id",
        searchable: true,
        sort: true,
        formatter: (cellContent, row) => {
            if (cellContent === null) {
                return <>N/A</>
            }
            else {
                return <>{cellContent}</>
            }
        }
    },
    {
        dataField: "merchId",
        text: "Merchant Id",
        searchable: true,
        sort: true,
        formatter: (cellContent, row) => {
            if (cellContent === null) {
                return <>N/A</>
            }
            else {
                return <>{cellContent}</>
            }
        }
    },
    {
        dataField: "",
        text: "Merchant Name",
        searchable: true,
        sort: true,
        formatter: (cellContent, row) => {
            if (row.merchId === null) {
                return <>N/A</>
            }
            else {
                return (
                    <>
                        {merchantList.map((merchant, i) => {
                            if (row.merchId === merchant.merchantId) {
                                return <div key={i}>{merchant.merchantName}</div>
                            }
                        })}
                    </>
                )
            }

        }
    },
    {
        dataField: "userId",
        text: "User Id",
        searchable: true,
        sort: true,

    },
    {
        dataField: "",
        text: "Username",
        sort: true,
        formatter: (cellContent, row) => {
            return (
                <>
                    {userList.map((user, i) => {
                        if (row.userId === user.userId) {
                            return <div key={i}>{user.userName}</div>
                        }
                    })}
                </>
            )
        }
    },
    {
        dataField: "",
        text: "Email",
        sort: true,
        formatter: (cellContent, row) => {
            return (
                <>
                    {userList.map((user, i) => {
                        if (row.userId === user.userId) {
                            return <div key={i}>{user.email}</div>
                        }
                    })}
                </>
            )
        }
    },
    {
        dataField: "date",
        text: "Date",
        searchable: true,
        sort: true,
        formatter: (cellContent, row) => {
            return (
                <span>
                    {dateFormat(cellContent)}
                </span>
            )
        }
    },
    {
        dataField: "refPage",
        text: "Reference Page",
        searchable: true,
        sort: true,
    },
    {
        dataField: "action",
        text: "Action",
        searchable: true,
        sort: true,
    },
    {
        dataField: "ip",
        text: "IP Address",
        searchable: true,
        sort: true,
    },
    {
        dataField: "deviceName",
        text: "Device Name",
        searchable: true,
        sort: true,
    },
]
export default UserLogsColumns