
import React, { useState, useEffect } from "react"
import {
  Row,
  Col,
  Card,
  Form,
  CardBody,
  Label
} from "reactstrap"
import Dropzone from "react-dropzone"
import { Link } from "react-router-dom"
import { fileUpload } from "store/actions"
import { connect } from "react-redux"
const FileUpload = (props) => {

  const [selectedFiles, setselectedFiles] = useState([])
  const [selectedFilesView, setselectedFilesView] = useState([])
  useEffect(() => {
    if (selectedFiles.length !== 0) {
      props.onChange(selectedFiles)
    }
  }, [selectedFiles])

  useEffect(()=>{
    if(props.prDetails.EmailSettings.Attachment.length !== 0){
      setselectedFilesView(props.prDetails.EmailSettings.Attachment)
    }
  },[props.prDetails])
  
  useEffect(()=>{
  },[selectedFilesView])

  function handleAcceptedFiles(files) {
    files.map(file =>
      Object.assign(file, {
        preview: URL.createObjectURL(file),
        formattedSize: formatBytes(file.size),
      })
    )
    setselectedFiles(files)

  }

  /**
   * Formats the size
   */
  function formatBytes(bytes, decimals = 2) {
    if (bytes === 0) return "0 Bytes"
    const k = 1024
    const dm = decimals < 0 ? 0 : decimals
    const sizes = ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"]

    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + " " + sizes[i]
  }

  return (

    <div >
      <Row>
        <Col className="col-12">
          <Card>
            <Col>
             {selectedFiles.length !== 0?
              <button
              onClick={() => {
                setselectedFiles([])
              }}
              type="button"
              className="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>:null}
            </Col>
            <CardBody>

              <Form>
                {selectedFilesView.length !== 0 ? <Label className="color-active text-size">Files Uploaded</Label>:null}
                {selectedFilesView ?
                selectedFilesView.map((x,i)=>{
                  return(
                    <Row key={i} className="mb-2">
                      <Col className="color-active">{x}</Col>
                    </Row>
                  )
                }): null}
                <Dropzone
                  accept="image/png, image/jpeg, image/jpg, image/txt"
                  maxFiles={3}
                  maxSize={1e+7}
                  onDrop={acceptedFiles => {
                    handleAcceptedFiles(acceptedFiles)
                  }}
                >
                  {({ getRootProps, getInputProps }) => (
                    <div className="dropzone">
                      <div
                        className="dz-message needsclick mt-2"
                        {...getRootProps()}
                      >
                        <input {...getInputProps()} />
                        <div className="mb-3">
                          <i className="display-4 text-muted bx bxs-cloud-upload" />
                        </div>
                        <h4>Drop files here or click to upload.</h4>
                      </div>
                    </div>
                  )}
                </Dropzone>

                <div className="dropzone-previews mt-3" id="file-previews">

                  {selectedFiles.map((f, i) => {
                    return (
                      <Card
                        className="mt-1 mb-0 shadow-none border dz-processing dz-image-preview dz-success dz-complete"
                        key={i + "-file"}
                      >
                        <div className="p-2">
                          <Row className="align-items-center">
                            <Col className="col-auto">
                              <img
                                data-dz-thumbnail=""
                                height="80"
                                className="avatar-sm rounded bg-light"
                                alt={f.name}
                                src={f.preview}
                              />
                            </Col>
                            <Col>
                              <Link
                                to="#"
                                className="text-muted font-weight-bold"
                              >
                                {f.name}
                              </Link>
                              <p className="mb-0">
                                <strong>{f.formattedSize}</strong>
                              </p>
                            </Col>
                          </Row>
                        </div>
                      </Card>
                    )
                  })}
                  
                </div>
              </Form>

              {/* <div className="text-center mt-4">
                <button
                  type="button"
                  className="btn btn-primary waves-effect waves-light"
                >
                  Send Files
                    </button>
              </div> */}
            </CardBody>
          </Card>
        </Col>
      </Row>

    </div>

  )
}
const mapStateToProps = state => {
  return {
    paymentRequestDeatils: state.paymentRequests.paymentRequestDetail,
  }
}
export default connect(mapStateToProps)(FileUpload)
