import React from 'react'
import ColumnWithDataLabels from "./ColumnWithDateLabels"
import {
    Row,
    Col,
    Card,
    CardBody,
    CardTitle,
  
  } from "reactstrap"
function TopMerchantGraph() {
    return (
       <React.Fragment>
           
           <Col lg={12}>
              <Card  className="mini-stats-wid">
                <CardBody>
                  <CardTitle className="mb-4">
                   Top 10 Merchants{" "}
                  </CardTitle>
                  <ColumnWithDataLabels />
                </CardBody>
              </Card>
            </Col>
         
       </React.Fragment>
    )
}

export default TopMerchantGraph
