import React from 'react'
import {  Container  } from "reactstrap"
import Breadcrumbs from "../../components/Common/Breadcrumb"
import PlacerLogsTable from "./PlacerLogsTable"
function index() {
    return (
        <React.Fragment>
        <div className="page-content">
            <Container fluid>

                <Breadcrumbs title="Logs " breadcrumbItem=" PROJECT PLACER /ERROR" />
                <PlacerLogsTable />
            </Container>
        </div>
    </React.Fragment>
    )
}

export default index
