import React, { useEffect, useState, useContext, forwardRef, useImperativeHandle } from 'react'
import {
    Col,
    Form,
    FormGroup,
    Label,
    Row,
    Button,
    InputGroup,
    InputGroupAddon,
    Modal,
    ModalBody
} from "reactstrap"
import "flatpickr/dist/themes/material_blue.css"
import { useForm } from 'react-hook-form';
import { isEmpty } from 'lodash'
import Items from '../Items'
import { v4 as uuidv4 } from 'uuid'
import CustomerPopup from "../../../components/Customer/CustomerList"

export const FirstPaymentRequestDetails = forwardRef((props, ref) => {

    const {
        prDetails,
        itemDetails,
        handlePaymentRequestChange,
        handlePurchasedItemChange,
        toggleTabProgress,
        activeTabProgress,
        loadSubmitNowButton,
        merchant
    } = props

    const [showPurchase, setShowPurchase] = useState(false)
    const [modal_xlarge, setmodal_xlarge] = useState(false)
    const { register, handleSubmit, errors, reset, watch } = useForm();
    const [mobilePlus, setMobilePlus] = useState("+974")
    const [mobile, setMobile] = useState(0)
    const [merchantCardTypes, setMerchantCardTypes] = useState([])

    useEffect(() => {
        //console.log(merchant)
        if (merchantCardTypes.length === 0  && merchant !== undefined && Array.isArray(merchant.paymentGatewayEnrollments)) {
            merchant.paymentGatewayEnrollments.map((pgEnrollments, index) => {
                //console.log(pgEnrollments.commissionSettings)
                if (pgEnrollments.isActive === true) {
                    pgEnrollments.commissionSettings.map((cards, i) => {
                        //console.log(cards.paymentMethod)
                        setMerchantCardTypes(merchantCards => [
                            ...merchantCards,
                            cards.paymentMethod
                        ])
                    })
                }
            })
        }
    }, [merchant])

    useEffect(() => {
    }, [merchantCardTypes])

    useEffect(() => {
        if (prDetails.CustomerDetails.Mobile !== undefined && prDetails.CustomerDetails.Mobile !== "") {
            let spilt = prDetails.CustomerDetails.Mobile.split("-")
            setMobilePlus(spilt[0])
            setMobile(spilt[1])
        }
        if (prDetails.CustomerDetails.Name !== undefined && prDetails.CustomerDetails.Name !== "") {
            setmodal_xlarge(false)
        }
    }, [prDetails])

    function tog_xlarge() {
        setmodal_xlarge(!modal_xlarge)
        removeBodyCss()
    }

    function removeBodyCss() {
        document.body.classList.add("no_padding")
    }

    function handleChange(changes) {
        handlePaymentRequestChange({ ...prDetails, ...changes })
    }

    function handleChangeItem(changes) {
        handlePurchasedItemChange({ ...itemDetails, ...changes })
    }

    function handleChangeCustomerDetails(changes) {
        handleChange({ CustomerDetails: { ...prDetails.CustomerDetails, ...changes } })
    }

    function handleChangePrSettingsDetails(changes) {
        handleChange({ PaymentRequestSettings: { ...prDetails.PaymentRequestSettings, ...changes } })
    }


    function handleChangeSingleItem(id, item) {
        const newItems = [...itemDetails.Items]
        const index = newItems.findIndex(i => i.id === id)
        newItems[index] = item
        handleChangeItem({ Items: newItems })
    }

    function handleAddItem() {
        const newItem = {
            id: uuidv4(),
            Qty: 0,
            ItemName: '',
            Amount: 0,
        }
        handleChangeItem({ Items: [...itemDetails.Items, newItem] })
    }

    function handleDeleteItem(id) {
        handleChangeItem({ Items: itemDetails.Items.filter(i => i.id !== id) })
    }

    const onSubmit = data => handleCheckNoErrors();

    function handleCheckNoErrors() {


        if (isEmpty(errors)) {
            toggleTabProgress(activeTabProgress + 1)
        }

    }

    const showPurchaseRow = () => {

        setShowPurchase(!showPurchase)
    }
    return (
        <>
            <Modal size="xl"
                isOpen={modal_xlarge}
                centered={true}
                toggle={() => {
                    tog_xlarge()
                }}>
                <div className="modal-header">
                    <h5
                        className="modal-title mt-0"
                        id="myLargeModalLabel"
                    >

                    </h5>
                    <button
                        onClick={() => {
                            setmodal_xlarge(false)
                        }}
                        type="button"
                        className="close"
                        data-dismiss="modal"
                        aria-label="Close"
                    >
                        <span caria-hidden="true" className="color-danger my-auto"> x</span>
                    </button>
                </div>
                <ModalBody>
                    < CustomerPopup
                        onChange={value => setmodal_xlarge(value)} />
                </ModalBody>
            </Modal>
            <Form onSubmit={handleSubmit(onSubmit)} id='my-form'>
                <p style={{ color: "#5c8FEC" }}><small><b >BILLING INFORMATION</b></small></p>
                <Row>
                    <Col lg="2">
                        <div className="mb-3">
                            <Button
                                onClick={() => setmodal_xlarge(true)}
                                color="success"
                                className="btn-block inner"
                                style={{ width: "100%", height: "56px" }}
                            >
                                <i className="fas fa-plus fas-2x" />  Customer
                            </Button>
                        </div>
                    </Col>
                    <Col lg="4">
                        <div className="form-floating mb-3">
                            <input type="text"
                                className="form-control bg-color-disable color-text"
                                value={prDetails.CustomerDetails.Name || " "}
                                onChange={(e) => handleChangeCustomerDetails({ Name: e.target.value })}
                                id="name"
                                name="Name"
                                placeholder="Enter Name"
                                ref={register({ required: true })}
                                readOnly
                            />
                            <label htmlFor="floatingnameInput"> Name</label>
                            {errors.Name && errors.Name.type === "required" && <small className="color-danger">This is required</small>}

                        </div>

                    </Col>


                    <Col lg="3">
                        <div className="form-floating mb-3">
                            <input
                                type="text"
                                className="form-control bg-color-disable color-text"
                                id="email"
                                name="Email"
                                placeholder="Enter Email"
                                value={prDetails.CustomerDetails.Email || " "}
                                readOnly
                                onChange={(e) => handleChangeCustomerDetails({ Email: e.target.value })}
                                ref={register({
                                    required: {
                                        value: true,
                                        message: "This is required"
                                    },
                                    pattern: {
                                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                        message: "invalid email address"
                                    }
                                })}
                            />
                            <label htmlFor="floatingnameInput"> Email</label>
                            {errors.Email && errors.Email.type === "required" && <small className="color-danger">{errors.Email.message}</small>}
                            {errors.Email && errors.Email.type === "pattern" && <small className="color-danger">{errors.Email.message}</small>}

                        </div>
                    </Col>


                    <Col lg="1">

                        <div className="form-floating mb-3" >
                            <input
                                type="text"
                                className="form-control bg-color-disable color-text"
                                id="mobilePlus"
                                name="mobilePlus"
                                readOnly
                                placeholder="+974"
                                value={mobilePlus}
                                onChange={(e) => handleChangeCustomerDetails({ Mobile: e.target.value })}
                                ref={register({ required: true })}
                            />
                            {/* <select className="form-select form-control">
                                <option value="00974" defaultValue>+974</option>
                                <option value="00963" defaultValue>+963</option>
                            </select> */}
                            <label htmlFor="floatingnameInput">Area</label>
                        </div>


                    </Col>

                    <Col lg="2">


                        <div className="form-floating mb-3" >
                            <input
                                type="number"
                                className="form-control bg-color-disable color-text"
                                id="mobile"
                                name="mobile"
                                readOnly
                                placeholder="Enter Mobile"
                                value={mobile}
                                onChange={(e) => handleChangeCustomerDetails({ Mobile: e.target.value })}
                                ref={register({ required: true })}
                            />
                            <label htmlFor="floatingnameInput">Mobile Number</label>
                            {errors.Mobile && errors.Mobile.type === "required" && <small className="color-danger">This is required</small>}

                        </div>


                    </Col>

                </Row>

                <p style={{ color: "#5c8FEC" }}><small><b>PAYMENT DETAILS</b></small></p>
                <Row>
                    <Col lg="4">
                        <Row >
                            <Col md="4" >
                                {/* <InputGroupAddon className="mr-3"> */}
                                <div className="form-floating mb-3" style={{ width: "100%" }}>
                                    <select
                                        className="form-select form-control bg-color-amount border-none  input-text-size-currency bold"
                                        value={prDetails.Currency}
                                        name="Currency"
                                        onChange={(e) => handleChange({ Currency: e.target.value })}
                                        ref={register({ required: true })}
                                    >
                                        <option value="QAR" defaultValue>QAR</option>

                                    </select>
                                    <label htmlFor="floatingnameInput">Currency</label>
                                    {errors.Currency && errors.Currency.type === "required" && <small className="color-danger">This is required</small>}

                                </div>


                                {/* </InputGroupAddon> */}
                            </Col>
                            <Col md="8" >
                                <div className="form-floating mb-3 " >
                                    <input type="number"
                                        className="form-control bg-color-amount input-text-size-amount bold border-none"
                                        id="amount"
                                        min="0"
                                        step=".01"
                                        placeholder="Enter Amount"
                                        value={prDetails.Amount}
                                        onChange={(e) => handleChange({ Amount: parseFloat(e.target.value) || '' })}

                                        name="Amount"
                                        ref={register({
                                            required: {
                                                value: true,
                                                message: "This is required"
                                            },
                                            min: {
                                                value: 1,
                                                message: "Not a valid amount"
                                            }
                                        })}
                                    />
                                    <label htmlFor="floatingnameInput">Amount Due</label>
                                    {errors.Amount && errors.Amount.type === "required" && <small className="color-danger">This is required</small>}
                                    {errors.Amount && errors.Amount.type === "min" && <small className="color-danger">{errors.Amount.message}</small>}

                                </div>
                            </Col>
                        </Row>

                    </Col>


                    <Col lg="4">
                        {merchantCardTypes.length === 3 ?
                            <div className="form-floating mb-3" style={{ width: "100%" }}>
                                <select
                                    value={prDetails.PaymentRequestSettings.PaymentType}
                                    className="form-select form-control"
                                    onChange={(e) => handleChangePrSettingsDetails({ PaymentType: e.target.value })}
                                    ref={register({ required: true })}
                                    name="PaymentType"
                                >
                                    <option value="" defaultValue hidden="hidden">Choose here</option>
                                    <option value="Debit, Credit and OMM" >Debit, Credit and OMM</option>
                                    <option value="Debit and Credit">Debit and Credit</option>
                                    <option value="OMM">OMM</option>
                                    <option value="Credit">Credit</option>
                                    <option value="Debit">Debit</option>
                                </select>
                                <label htmlFor="floatingSelectGrid">Card Type</label>
                                {errors.PaymentType && errors.PaymentType.type === "required" && <small className="color-danger">This is required</small>}


                            </div>
                            : null}

                        {merchantCardTypes.length === 2 ?
                            <div className="form-floating mb-3" style={{ width: "100%" }}>
                                <select
                                    value={prDetails.PaymentRequestSettings.PaymentType}
                                    className="form-select form-control"
                                    onChange={(e) => handleChangePrSettingsDetails({ PaymentType: e.target.value })}
                                    ref={register({ required: true })}
                                    name="PaymentType"
                                >
                                    <option value="" defaultValue hidden="hidden">Choose here</option>
                                    <option value="Debit and Credit">Debit and Credit</option>
                                    <option value="Debit">Debit</option>
                                    <option value="Credit">Credit</option>
                                </select>
                                <label htmlFor="floatingSelectGrid">Card Type</label>
                                {errors.PaymentType && errors.PaymentType.type === "required" && <small className="color-danger">This is required</small>}


                            </div>
                            : null}


                    </Col>


                    <Col lg="4">

                        <div className="form-floating mb-3">
                            <select
                                value={prDetails.PaymentRequestSettings.Language}
                                className="form-select form-control"
                                onChange={(e) => handleChangePrSettingsDetails({ Language: e.target.value })}
                                ref={register({ required: true })}
                                name="Language"
                            >
                                <option value="" selected="selected" hidden="hidden">Choose here</option>
                                <option value="En">English</option>
                                <option value="Ar">Arabic</option>

                            </select>
                            <label htmlFor="floatingSelectGrid">Language</label>
                            {errors.Language && errors.Language.type === "required" && <small className="color-danger">This is required</small>}

                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col lg="4">

                        <div className="form-floating mb-3">
                            <input type="text"
                                className="form-control"
                                id="transactionreference"
                                placeholder="Enter Transaction reference"
                                value={prDetails.TransactionReference}
                                onChange={(e) => handleChange({ TransactionReference: e.target.value })}
                            />
                            <label htmlFor="floatingnameInput">Transaction Reference</label>
                        </div>
                    </Col>

                    <Col lg="8">

                        <div className="form-floating mb-3">
                            <input type="text"
                                className="form-control"
                                id="name"
                                placeholder="Enter Name"
                                value={prDetails.Remarks}
                                onChange={(e) => handleChange({ Remarks: e.target.value })}
                            />
                            <label htmlFor="floatingnameInput">Remarks</label>
                        </div>
                    </Col>

                </Row>


                <div className="mb-3">

                    <div className="form-check">
                        <input className="form-check-input" type="checkbox" id="floatingCheck" onChange={showPurchaseRow} />
                        <label className="form-check-label" htmlFor="floatingCheck">
                            Add Item Breakdown
                        </label>
                    </div>
                </div>

                {showPurchase === true &&
                    <>
                        <p style={{ color: "#5c8FEC" }}><small><b>PURCHASE DETAILS</b></small></p>


                        <Row style={{ color: "white", background: "#5c8FEC", padding: "3px", marginLeft: "3px", marginRight: "3px" }} >
                            <Col lg="5"><small>Product or Service</small></Col>
                            <Col lg="2"><small>Quantity</small></Col>
                            <Col lg="3"><small>Amount</small></Col>
                            <Col lg="2"></Col>
                        </Row>
                        <br></br>

                        {itemDetails.Items.map((item, index) =>
                            <Items
                                key={index}
                                index={index}
                                item={item}
                                handleChangeSingleItem={handleChangeSingleItem}
                                handleDeleteItem={handleDeleteItem}
                                handleSubmit={handleSubmit}
                                onSubmit={onSubmit}
                                register={register}
                                errors={errors}
                            />
                        )}

                        <Button
                            onClick={() => handleAddItem()}
                            color="success"
                            className="mt-1"
                        >
                            Add Product or Service
                        </Button>
                    </>
                }

                <ul className="pager wizard twitter-bs-wizard-pager-link">
                    <li className=
                        {activeTabProgress === 1
                            ? "previous disabled"
                            : "previous"
                        }
                    >

                    </li>
                    <li
                        className={
                            activeTabProgress === 3 ? "next" : "next"
                        }
                    >

                        {activeTabProgress === 3 ?
                            <>
                                {loadSubmitNowButton()}
                            </>
                            :
                            <Button
                                type="submit"
                                color="primary"
                                id='my-form_submit_1'
                            > Next </Button>
                        }

                    </li>
                </ul>

            </Form>


        </>
    )
})



export default FirstPaymentRequestDetails
