import React, { useState, useEffect } from 'react'
import ExpressPayRequestModal from "./ExpressPayRequest"
import OverViewExpressPayRequest from "./OverViewExpressPayRequest"
import { Col, Row, Button, Spinner, Modal, ModalBody, Label } from "reactstrap"
import { toast } from "react-toastify"
import { setPaymentRequestExpress, setWhatAppLog, setCustomer, getMerchantDetail } from "../../store/actions"
import { connect } from "react-redux"
import { useDispatch } from "react-redux"
import AuthUser from "components/AuthUser/AuthUser"
import LoadingSpinnerThreeDots from "helpers/commonhelpers/loadingSpinner"
import { sendPaymentRequestEmailExpress } from "store/actions"
import dateFormat from '../../helpers/commonhelpers/dateFormat'
import toastr from "toastr"
import "toastr/build/toastr.min.css"
import SweetAlert from "react-bootstrap-sweetalert"

import validator from 'validator'
//Initial Values
const intialValues = {
    TransactionReference: "",
    PaymentRequestId: "",
    Amount: parseFloat(1).toFixed(2),
    Currency: "QAR",
    PaymentRequestSettings: {
        PaymentType: "Debit, Credit and OMM",
        Language: "EN",
        LinkValidUpto: "2020-01-01T01:01:01",
        LinkValidity: parseFloat(0),
        TerminalId: ""
    },
    CustomerDetails: {
        Name: "",
        Mobile: "",
        Email: "",
        Type: "New"
    },
    EmailSettings: {
        SubjectLine: "",
        Attachment: [],
        SentTo: []
    },
    Remarks: "",
    GeneratedLinkLong: "",
    GeneratedLinkLongEncrypted: "",
    GeneratedLinkShort: "",
    GeneratedLinkStatus: "",
    MerchantId: "",
    ItemId: "",
    ItemSource: "",
    PaymentRequestDate: "2020-01-01T01:01:01",
    LastPayment: "",
    PaymentStatus: "Unpaid",
    PaymentGatewayId: "",
    Commission: {
        Type: "",
        Value: parseFloat(0),
        TotalCommission: parseFloat(0)
    }
}
function index(props) {
    //Constants-----------------------------------------------------------------------------------------------
    const [{ Amount, Currency, Name, Email, Mobile,
        Type, MobilePlus, Language, PaymentRequestObjectId, CreatedBy, PaymentType,
        Remarks, step, modal_large, shortLink, SubjectLine, emailLoading, boolEmailAlertTrue, boolEmailAlertFalse, activeTabTracker }, setPaymentRequestDetails] = useState({
            ...intialValues,
            Amount: parseFloat(1).toFixed(2),
            Currency: "QAR",
            Remarks: "",
            MobilePlus: "+974",
            Name: "",
            Email: "",
            Remarks: "",
            Mobile: "",
            Type: "Recurring",
            step: 0,
            modal_large: false,
            shortLink: "",
            PaymentRequestObjectId: "",
            SubjectLine: "Payment Request for ",
            boolEmailAlertTrue: false,
            boolEmailAlertFalse: false,
            PaymentType: "Debit, Credit and OMM",
            Language: "EN",
            activeTabTracker: "1"

        })
    let form = null;
    const dispatch = useDispatch();
    const [loading, setLoading] = useState(props.paymentRequestLoading)
    const [user, setUser] = useState(AuthUser())
    const [emailSent, setEmailSent] = useState(false)
    const [merchant, setMerchant] = useState()
    const [disable, setDisable] = useState(true)
    //UseEffects---------------------------------------------------------------------------------------
    //Use Effect for setting subjectLine with customer name
    useEffect(() => {
        dispatch(getMerchantDetail(user.merchantId))
    }, [])

    useEffect(() => {
        if (props.merchantDetail !== undefined) {
            setMerchant(props.merchantDetail)
        }
    }, [props.merchantDetail])

    useEffect(() => {
    }, [merchant])

    useEffect(() => {
        if (Name !== "" || Name !== undefined) {
            setPaymentRequestDetails(formRequest => (
                {
                    ...formRequest,
                    SubjectLine: "Payment Request for " + Name
                }
            ));
        }
    }, [Name])

    useEffect(() => {
    }, [props.paymentRequestLoading])
    useEffect(() => {
        if (props.paymentRequestDetailExpress?._id !== undefined && emailLoading !== undefined) {
            handleEmailSending(emailLoading)
        }
    }, [emailLoading, props.paymentRequestDetailExpress?._id])

    //Use Effect for Payment Request
    useEffect(() => {
        let cus = {
            Name,
            Email,
            Mobile: MobilePlus + "-" + Mobile.toString(),
            RegistrationDate: "2020-01-01T01:01:01",
            Type,
            MerchantId: user.merchantId,
            IsActive: true
        }
        if (step === 1 && Amount !== 0 && Amount !== "" && Amount !== " " && Amount >= 1 && validator.isEmail(Email)) {
            handlePaymentRequest();
            if (activeTabTracker === "1") {
                dispatch(setCustomer(cus))
            }
        }
        if (step === 0) {
            setPaymentRequestDetails(formRequest => ({
                ...formRequest,
                Amount: parseFloat(1).toFixed(2),
                Currency: "QAR",
                MobilePlus: "+974",
                Name: "",
                Email: "",
                Mobile: 0,
                PaymentRequestObjectId: "",

            }))
        }

    }, [step, activeTabTracker])

    //Use Effect for checking if shortLink was returned from API, before proceeding to overview page
    useEffect(() => {
        if (props.paymentRequestLoading === false && props.paymentRequestDetailExpress?.generatedLinkShort !== undefined) {
            setPaymentRequestDetails(formRequest => ({
                ...formRequest,
                shortLink: props.paymentRequestDetailExpress.generatedLinkShort
            }))
            setLoading(props.paymentRequestLoading)
        }
    }, [props.paymentRequestLoading, props.paymentRequestDetailExpress?.generatedLinkShort])

    useEffect(() => {
        if (props.emailStatus !== undefined) {
            if (props.emailStatus === true) {
                setPaymentRequestDetails(formRequest => ({
                    ...formRequest,
                    boolEmailAlertTrue: true,

                }))
                setEmailSent(true)
            }
            else {
                setPaymentRequestDetails(formRequest => ({
                    ...formRequest,
                    boolEmailAlertFalse: true,
                }))
                setEmailSent(true)
            }
        }
    }, [props.emailStatus])

    useEffect(() => {
        if (modal_large === true) {
            if (props.newCustomer === true) {
                showToastSuccess("New Customer " + Name + " Created")
            }
            if (props.newCustomer === false) {
                showToastInfo("Customer " + Name + " already in customer list.")
            }
        }
    }, [props.newCustomer])

    useEffect(() => { }, [boolEmailAlertTrue, boolEmailAlertFalse])

    useEffect(() => {
        if (modal_large === false) {
            setPaymentRequestDetails(formRequest => ({
                ...formRequest,
                Amount: parseFloat(1).toFixed(2),
                Currency: "QAR",
                MobilePlus: "+974",
                Name: "",
                Email: "",
                Mobile: 0,
                step: 0,
                PaymentRequestObjectId: "",

            }))
            setEmailSent(false)
        }
    }, [modal_large])

    //Functions--------------------------------------------------------------------------------------------
    //Function for opening modal
    function tog_large() {
        setPaymentRequestDetails(formRequest => ({
            ...formRequest,
            modal_large: !modal_large
        }))
        removeBodyCss()
    }

    //Function for closing the modal
    function removeBodyCss() {
        document.body.classList.add("no_padding")
    }

    //Function for Payment Request
    const handlePaymentRequest = () => {
        let dateToday = new Date();
        dateToday.setHours(dateToday.getHours() + 3);
        const valuesToSend = {
            TransactionReference: "",
            PaymentRequestId: "",
            Amount: parseFloat((Math.round(Amount * 100) / 100).toFixed(2)),
            Currency,
            PaymentRequestSettings: {
                PaymentType: PaymentType,
                Language: Language,
                LinkValidUpto: dateToday,
                LinkValidity: parseFloat(0),
                TerminalId: ""
            },
            CustomerDetails: {
                Name,
                Mobile: MobilePlus + "-" + Mobile.toString(),
                Email,
                Type
            },
            EmailSettings: {
                SubjectLine,
                Attachment: [],
                SentTo: []
            },
            Remarks,
            GeneratedLinkLong: "",
            GeneratedLinkLongEncrypted: "",
            GeneratedLinkShort: "",
            GeneratedLinkStatus: "",
            MerchantId: "",
            ItemId: "",
            ItemSource: "PORTAL",
            PaymentRequestDate: dateToday,
            LastPayment: "",
            IsExpress: true,
            PaymentStatus: "Unpaid",
            PaymentGatewayId: "",
            Commission: {
                Type: "",
                Value: parseFloat(0),
                TotalCommission: parseFloat(0)
            }
        }

        if (Name !== "" && Email !== "" && Mobile !== "" && (Amount !== 1 && Amount !== "" && Amount !== " ")) {
            dispatch(setPaymentRequestExpress(valuesToSend));
            setLoading(true)

            showToastSuccess("Payment Request created successfully.")
        }
    }

    const handleEmailSending = (value) => {

        setLoading(true);
        if (value === "all") {
            const data = {
                id: props.paymentRequestDetailExpress._id,
                recipient: null,
                actionType: "Initial Send of Payment Request"
            }
            dispatch(sendPaymentRequestEmailExpress(data));

        }
        else if (value === "user") {
            const data = {
                id: props.paymentRequestDetailExpress._id,
                recipient: user.email,
                actionType: "Initial Send of Payment Request"
            }
            dispatch(sendPaymentRequestEmailExpress(data));

        }
        else if (value === "whatsapp") {
            const data = {
                id: props.paymentRequestDetailExpress._id,
                recipient: null,
                actionType: "Initial Send of Payment Request"
            }
            dispatch(sendPaymentRequestEmailExpress(data));

            let mobile = MobilePlus + Mobile
            let newMobile = mobile.replace("-", "")
            let name = Name
            let newName = name.split(" ").join("%20")
            let whatAppMessage = "Payment%20Request%20for%20" + newName + ".%20" + props.paymentRequestDetailExpress.generatedLinkShort
            let whatsAppUrl = "https://wa.me/" + newMobile + "?text=" + whatAppMessage
            const whatsApp = {
                PhoneNumber: newMobile,
                pr_id: props.paymentRequestDetailExpress._id,
                Message: whatAppMessage
            }
            dispatch(setWhatAppLog(whatsApp))
            window.location.href = whatsAppUrl
        }
        else {
            setPaymentRequestDetails(formRequest => ({
                ...formRequest,
                boolEmailAlertFalse: true
            }))
        }
        setPaymentRequestDetails(formRequest => ({
            ...formRequest,
            emailLoading: undefined
        }))

    }


    const showToastSuccess = (message) => {


        toastr.options = {
            positionClass: "toast-top-right",
            timeOut: 10000,
            extendedTimeOut: 1000,
            closeButton: true,
            debug: false,
            progressBar: true,
            preventDuplicates: true,
            newestOnTop: true,
            showEasing: "swing",
            hideEasing: "linear",
            showMethod: "fadeIn",
            hideMethod: "fadeOut",
            showDuration: 300,
            hideDuration: 300
        }

        toastr.success(message)
    }

    const showToastInfo = (message) => {
        toastr.options = {
            positionClass: "toast-top-right",
            timeOut: 10000,
            extendedTimeOut: 1000,
            closeButton: true,
            debug: false,
            progressBar: true,
            preventDuplicates: true,
            newestOnTop: true,
            showEasing: "swing",
            hideEasing: "linear",
            showMethod: "fadeIn",
            hideMethod: "fadeOut",
            showDuration: 300,
            hideDuration: 300
        }

        toastr.info(message)
    }



    //Switch Case--------------------------------------------------------------------------------------------------------
    switch (step) {
        case 0:
            form = <ExpressPayRequestModal
                merchant={merchant}
                onChange={value => setPaymentRequestDetails(value)} />
            break
        case 1:
            form = <OverViewExpressPayRequest
                Amount={Amount}
                Currency={Currency}
                Email={Email}
                Remarks={Remarks}
                Name={Name}
                PaymentRequestObjectId={PaymentRequestObjectId}
                shortLink={shortLink}
                boolEmailAlertTrue={boolEmailAlertTrue}
                boolEmailAlertFalse={boolEmailAlertFalse}
                onChange={value => setPaymentRequestDetails(value)} />
            break
        default:
            form = <ExpressPayRequestModal />
    }

    return (
        <React.Fragment>
            {/* { modal_large ?
                <SweetAlert
                    warning
                    confirmBtnText="Close"
                    confirmBtnBsStyle="danger"
                    title="Sorry, this service is temporarily unavailable."
                    onConfirm={() => {
                        // setDisable(false)
                        tog_large()
                        // window.location = "/dashboard"
                    }}
                >

                </SweetAlert>
                : null} */}
                <>
                    <Modal
                        size="lg"
                        isOpen={modal_large}
                        centered={true}
                    >
                        <div className="modal-header">

                            <Label className="my-auto">EXPRESS PAYMENT REQUEST</Label>
                            <h5
                                className="modal-title mt-0 color-danger"
                                id="myLargeModalLabel"
                            >
                            </h5>
                            {step === 0 ?
                                <button
                                    onClick={() => {
                                        setPaymentRequestDetails(formRequest => ({
                                            ...formRequest,
                                            modal_large: false
                                        }))
                                    }}
                                    type="button"
                                    color="danger"
                                    className="close"
                                    data-dismiss="modal"
                                    aria-label="Close"
                                >
                                    <span caria-hidden="true" className="color-danger my-auto"> x</span>
                                </button>
                                : null}
                        </div>
                        <ModalBody>
                            {loading ?
                                <Row className="spinner-custom mx-auto my-auto ">
                                    <LoadingSpinnerThreeDots />
                                </Row>
                                : form}
                        </ModalBody>
                    </Modal>


                    <Row >
                        <Col sm="12" >
                            <Button
                                color="warning"
                                data-toggle="modal"
                                outline
                                data-target=".bs-example-modal-lg"
                                className="express-payment"
                                //className="mr-1 waves-effect waves-light my-3"
                                onClick={() => {
                                    tog_large()
                                }}
                            >
                                <i className="bx bx-webcam fa" />
                                Express Payment Request
                            </Button></Col>
                    </Row>
                </>
            

        </React.Fragment>

    )
}
const mapStateToProps = state => {
    return {
        customer: state.Customer.customer,
        loading: state.Customer.loading,
        customersList: state.Customer.customerList,
        paymentRequestDetailExpress: state.paymentRequests.paymentRequestDetailExpress,
        paymentRequestLoading: state.paymentRequests.loading,
        emailStatus: state.paymentRequests.emailStatusExpress,
        newCustomer: state.Customer.customerCreated,
        merchantDetail: state.Merchants.merchantDetail,
    }
}


export default connect(mapStateToProps)(index)
