import React, { useEffect, useState } from 'react'
import { Col, Card, CardBody, Container, Row, Spinner, Button } from "reactstrap"
import PropTypes from "prop-types"
import { withRouter } from "react-router-dom"
import { connect, useDispatch } from "react-redux"
import Header from './Header'
import PurchaseDetails from './PurchaseDetails'
import PaymentDetails from './PaymentDetails'
import PaidInvoice from './PaidInvoice'
import PaymentMethod from './PaymentMethod'
import Footer from './Footer'
import FooterBanner from './FooterBanner'
import LoadingSpinnerThreeDots from '../../helpers/commonhelpers/loadingSpinner'
import LanguageDropdown from "../../components/CommonForBoth/TopbarDropdown/LanguageDropdown"
import { withTranslation } from "react-i18next"
import { getPaymentRequestDetailPublic } from "store/actions"
import { setPaymentMethod } from 'store/actions'
import ExpiredInvoice from './ExpiredInvoice'
import ServicesOffered from './ServicesOffered'
import MoreInformation from './MoreInformation'
import SweetAlert from "react-bootstrap-sweetalert"
import { isEmpty } from 'lodash'
export const PaymentMethodContext = React.createContext()
//TODO: rtl
const currentLanguage = localStorage.getItem("I18N_LANGUAGE")


const paymentMethodsettings = {
   objectId: "",
   paymentType: "credit"
}
const Invoice = (props) => {

   const {
      paymentRequestDetail,
      loading,
      paymentRedirectionLink,
      match: { params },
      onGetPaymentRequestDetail,
      error
   } = props

   
   const [isLoaded, setIsLoaded] = useState(loading);
   const [hasSuccess, setHasSuccess] = useState(false);
   const [pr, setPr] = useState()
   const dispatch = useDispatch()
   const [defaultLang, setDefaultLang] = useState(currentLanguage)
   const [redirectionLink, setRedirectionLink] = useState(undefined)
   const [errorResponse, setErrorResponse] = useState(undefined)

   ////useEffect
   useEffect(() => {
      if (params && typeof params.id === "string") {
         onGetPaymentRequestDetail(params.id)
      }

   }, [params, onGetPaymentRequestDetail])


   useEffect(() => {

      if(error !==undefined && !isEmpty(error) && error !== {}){
         setErrorResponse(error)
      }
   

   }, [error])

   useEffect(() => {
    
   }, [errorResponse])

 
   useEffect(() => {
      if (paymentRequestDetail) {
         setPr(paymentRequestDetail)
        
      }
   }, [paymentRequestDetail])

   useEffect(() => {

      if (pr?.paymentRequestDetails?.paymentRequestSettings?.language !== undefined) {
         const configuredLang = pr?.paymentRequestDetails?.paymentRequestSettings?.language.toLowerCase()

         if (currentLanguage !== configuredLang)
            setDefaultLang(configuredLang)

      }

   }, [pr])

   useEffect(() => {
      if (defaultLang && defaultLang !== undefined) {
         localStorage.setItem("i18nextLng", defaultLang)
         localStorage.setItem("I18N_LANGUAGE", defaultLang)
      }
   }, [defaultLang])


   useEffect(() => {

      setIsLoaded(loading)
      checkForAnySuccessfulTransactions()
   }, [loading])



   useEffect(() => {
      setRedirectionLink(paymentRedirectionLink)
   }, [paymentRedirectionLink])


   useEffect(() => {

      if (redirectionLink && redirectionLink != '') {
         window.open(redirectionLink, '_self', 'toolbar=0,location=0,menubar=0')
      }

   }, [redirectionLink])


   ////functions
   function handleCreditCard() {

      const data = {
         objectId: paymentRequestDetail.paymentRequestDetails?._id,
         paymentType: "credit"
      }
      if (data.objectId)
         dispatch(setPaymentMethod(data))
   }

   function handleDebitCard() {

      const data = {
         objectId: paymentRequestDetail?.paymentRequestDetails?._id,
         paymentType: "debit"
      }

      if (data.objectId)
         dispatch(setPaymentMethod(data))
   }

   function handleOoredoo() {

      const data = {
         objectId: paymentRequestDetail?.paymentRequestDetails?._id,
         paymentType: "omm"
      }
      dispatch(setPaymentMethod(data))
   }

   function checkForAnySuccessfulTransactions() {
      const hasSuccessStatus = paymentRequestDetail?.paymentHistoryDetails?.some(x => x.isSuccessful === true)
      if (hasSuccessStatus) {
         setHasSuccess(hasSuccessStatus)
      }
    
   }



   function renderMethodContent() {

      if (paymentRequestDetail?.paymentRequestDetails?.generatedLinkStatus != "Expired") {

         return (
            <>
               <Row >
                  <PaymentDetails amount={paymentRequestDetail?.paymentRequestDetails?.amount}
                     currency={paymentRequestDetail?.paymentRequestDetails?.currency}
                     termsConditionUrl={paymentRequestDetail?.merchantDetails?.termsConditionUrl} />
               </Row>


               <Row >
                  <PaymentMethodContext.Provider value={PaymentMethodContextValue}>
                     <PaymentMethod paymentRequestDetails={paymentRequestDetail?.paymentRequestDetails}
                     />
                  </PaymentMethodContext.Provider>
               </Row>
            </>
         )
      }
      else {
         return (
            <>
               <Row className="mb-2 mt-2">

                  <ExpiredInvoice
                     amount={paymentRequestDetail?.paymentRequestDetails?.amount}
                     currency={paymentRequestDetail?.paymentRequestDetails?.currency}
                  />
               </Row>
            </>
         )
      }
   }



   ///context
   const PaymentMethodContextValue = {
      handleCreditCard,
      handleDebitCard,
      handleOoredoo
   }
   return (


      <React.Fragment >
         <div style={{ "position": "absolute", "right": "0", "top": "0", "zIndex": "100" }}>
            <LanguageDropdown />
         </div>

         {errorResponse !== undefined && errorResponse !== {} ?
            <>
               <SweetAlert
                  error
                  title=""
                  onConfirm={() => {
                      setErrorResponse(undefined)
                      window.location.reload()
                  }}
               >
                  {errorResponse}
               </SweetAlert>
            </>
            : null
         }
         <div >
            <Col sm="12" md={{ size: 6, offset: 3 }}>

               <div>

                  <Container fluid>
                     {isLoaded ?

                        <LoadingSpinnerThreeDots />
                        :


                        < Card >
                           <CardBody>


                              <div className="justify-content-center container-alignment">
                                 <Row >
                                    <Header paymentRequestDetail={paymentRequestDetail?.paymentRequestDetails}

                                       merchantDetail={paymentRequestDetail?.merchantDetails}
                                    />

                                 </Row>

                                 <hr></hr>
                                 {paymentRequestDetail?.paymentRequestDetails?.paymentStatus === "Success" || hasSuccess ?

                                    <Row className="mb-1 mt-1">

                                       <PaidInvoice paidDetails={paymentRequestDetail?.paymentRequestDetails?.paidDetails}
                                          amount={paymentRequestDetail?.paymentRequestDetails?.amount}
                                          currency={paymentRequestDetail?.paymentRequestDetails?.currency}
                                       />
                                    </Row>

                                    :
                                    renderMethodContent()
                                 }

                              </div>
                              <div>
                                 <hr></hr>
                                 {paymentRequestDetail?.paymentRequestDetails?.isExpress === false &&
                                    <Row >
                                       <PurchaseDetails source={paymentRequestDetail?.paymentRequestDetails?.itemSource}
                                          purchasedItems={paymentRequestDetail?.purchasedItems}
                                       />
                                    </Row>

                                 }
                                 <Row>
                                    <MoreInformation servicesOffered={paymentRequestDetail?.merchantDetails?.services}
                                       remarks={paymentRequestDetail?.paymentRequestDetails?.remarks}
                                       user={paymentRequestDetail?.userDetails}
                                       merchant={paymentRequestDetail?.merchantDetails} />

                                 </Row>
                                 <Row >
                                    <Footer user={paymentRequestDetail?.userDetails}
                                       merchant={paymentRequestDetail?.merchantDetails} />
                                 </Row>
                                 <Row >
                                    <FooterBanner banner={paymentRequestDetail?.merchantDetails?.banner} />
                                 </Row>
                              </div>




                           </CardBody>
                        </Card>
                     }

                  </Container>

               </div>
            </Col>
         </div>
      </React.Fragment >
   )
}

Invoice.propTypes = {
   paymentRequestDetail: PropTypes.object,
   loading: PropTypes.bool,
   paymentRedirectionLink: PropTypes.string,
   match: PropTypes.object,
   onGetPaymentRequestDetail: PropTypes.func,
   error: PropTypes.object
}

const mapStateToProps = pr => (

   {
      paymentRequestDetail: pr.paymentRequests.paymentRequestDetail,
      loading: pr.paymentRequests.loading,
      paymentRedirectionLink: pr.paymentRequests.paymentRedirectionLink,
      error: pr.paymentRequests.error
   })

const mapDispatchToProps = dispatch => ({
   onGetPaymentRequestDetail: id => dispatch(getPaymentRequestDetailPublic(id)),
})

export default connect(
   mapStateToProps,
   mapDispatchToProps
)(withRouter(withTranslation()(Invoice)))

